import React, { Component } from "react";
import SaveBtn from "../../components/SaveBtn";
import Jumbotron from "../../components/Jumbotron";
import API from "../../utils/API";
import { Link } from "react-router-dom";
import { Col, Row, Container } from "../../components/Grid";
import { List, ListItem } from "../../components/List";
import { Input, TextArea, FormBtn } from "../../components/Form";

class Home extends Component {
  state = {
    articles: [],
    topic: "",
    startDate: "",
    endDate: ""
  };

  componentDidMount() {
    //this.loadBooks();
  }

  searchArticles = (topic, startDate, endDate) => {
    console.log(topic);
    console.log(startDate);
    console.log(endDate);
    API.searchArticles(topic,startDate,endDate)
      .then(res =>{
        console.log(res.data.response.docs);
        this.setState({ articles: res.data.response.docs, topic: "", startDate: "", endDate: "" })
      })
      .catch(err => console.log(err));
  };


  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleFormSubmit = event => {
    event.preventDefault();
    if (this.state.topic && this.state.startDate && this.state.endDate) {
      this.searchArticles(this.state.topic, this.state.startDate , this.state.endDate );

    }

  };

  saveArticle = (title,date,url) => {
    var articleData = {
      title:title,
      date:date,
      url:url
    }
    API.saveArticle(articleData)
      .then(res => this.loadBooks())
      .catch(err => console.log(err));
  };

  render() {
    return (
      <Container fluid>
        <Row fluid >
          <Col size="md-12">
            <Jumbotron>
            <h1>New York Times Article Scrubber</h1>
            <p>Search for and annotate articles of interest</p>
            </Jumbotron>
            <form>
            <h1>Search</h1>
              <Input
                value={this.state.topic}
                onChange={this.handleInputChange}
                name="topic"
                placeholder="Topic (required)"
              />
              <Input
                value={this.state.startDate}
                onChange={this.handleInputChange}
                name="startDate"
                placeholder="Start Date (required)"
              />
              <Input
                value={this.state.endDate}
                onChange={this.handleInputChange}
                name="endDate"
                placeholder="End Date (required)"
              />
              <FormBtn
                disabled={!(this.state.topic && this.state.startDate && this.state.endDate)}
                onClick={this.handleFormSubmit}
              >
                Submit Search
              </FormBtn>
            </form>
          </Col>
          </Row>
        <Row fluid>
          <Col size="md-12">
              <h1>Article Results</h1>
            {this.state.articles.length ? (
              <List>
                {this.state.articles.map(article => (
                  <ListItem key={article._id}>
                    <a href={article.web_url} target="_blank">
                      <strong>
                        {article.headline.main}
                      </strong>
                    </a>
                    <SaveBtn onClick={() => this.saveArticle(article.headline.main, article.pub_date, article.web_url)} />
                  </ListItem>
                ))}
              </List>
            ) : (
              <h3>No Results to Display</h3>
            )}
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Home;



